import React from 'react';
import { Text,View } from "react-native";
import { fireEvent, render, waitFor} from '@testing-library/react-native';
import renderer from 'react-test-renderer';
import {GameState} from '../../../types';
import {RootState} from '../../state/reducers/index';

import TextStroke from '../../../components/TextStroke';

//TextStroke component test.
describe('TextStroke', () =>{

  //Test that the component renders.
  it('renders without crashing', () =>{
    const titleComponent = render(
      <TextStroke
        color = {'white'}
        stroke = {2}
      >
        <Text>
          Suduko
        </Text>
      </TextStroke>
    ).toJSON();
    expect(titleComponent).toMatchSnapshot();
  })

  //Test that component renders with an invalid child element.
  it('renders with an invalid child element', () =>{
    const titleComponent = render(
      <TextStroke
        color = {'white'}
        stroke = {2}
      >
        Suduko
      </TextStroke>
    ).toJSON();
    expect(titleComponent).toBeTruthy();
  })

  //Test that component will render when its child has specified style properties.
  it('renders correctly when the child property has specified style properties', () =>{
    const titleComponent = render(
      <TextStroke
        color = {'white'}
        stroke = {2}
      >
        <Text
          style = {{color:'black'}}
        >
          Suduko
        </Text>
      </TextStroke>
    ).toJSON();
    expect(titleComponent).toBeTruthy();
  })

})